* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  font-size: 30px;
}

.info {
  display: flex;
  text-align: center;
}

.country-list {
  padding: 0;
}

li {
  list-style: none;
  display: flex;
}

img {
  width: 100px;
  margin-right: 20px;
  margin-bottom: 10px;
}

span {
  margin: 0;
  padding: 0;
  font-size: 20px;
  font-weight: 500;
}

